import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { BeanVariety } from './../bean/base-variety.bean';
// import { IsOrigin, IsLable2 } from './../../constant/codeValue';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { specialSymbol } from 'app/@uisftech/common/service/validator';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";

@Component({
  selector: 'app-base-variety-add',
  templateUrl: './base-variety-add.component.html',
  styleUrls: ['./base-variety-add.component.scss'],
  providers: [Commfunc]
})
export class BaseVarietyAddComponent implements OnInit {
  //传入的值
  @Input() inValue: string;
  //出参
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  //提示信息
  msgs: Message[] = [];
  beanVariety: BeanVariety = new BeanVariety();
  list: any = [];
  data1: any = [];
  data2: any = [];
  isOrigin: any = [];
  isLable2: any = [];
  userform: FormGroup; //校验 
  taskCategoryId: string;
  specialSymbol = specialSymbol;
  isDisable: boolean = false;
  show:boolean=false;
  //获取所有码值
  code: any;
  constructor(
    public httpService: ValuationHttpService,
    public router: Router,
    public activatedRoute: ActivatedRoute,
    public fb: FormBuilder,
    public commfunc: Commfunc
  ) {
    this.codeValues();
    this.isOrigin=this.code['IsOrigin'];
    this.isLable2=this.code['IsLable2'];
    //   //审核，工作流
    if (this.activatedRoute.snapshot.params.taskCategoryId) {
      this.taskCategoryId = this.activatedRoute.snapshot.params.taskCategoryId;
    }
  }

  ngOnInit() {
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para');
    }
    this.beanVariety = new BeanVariety();
    this.beanVariety.pageSize = 1;
    this.beanVariety.pageNum = 10;
    this.userform = this.fb.group({
      'intRateCgy': new FormControl('', [Validators.required, Validators.maxLength(3), specialSymbol]),
      'intRateName': new FormControl('', [Validators.required, Validators.maxLength(200), specialSymbol]),
      'intRateFlgCode': new FormControl('', Validators.required),
      'intRateSrcCode': new FormControl('', Validators.required),
      'txInf': new FormControl('', [Validators.nullValidator, Validators.maxLength(42), specialSymbol]),
    });
  }
   //码值
   codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //提交
  add() {
    if (this.isDisable) {
      return;
    }
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    this.isDisable = true;
    //处理工作流的参数
    this.beanVariety['taskCategoryId'] = this.taskCategoryId + '';
    this.httpService.addRateTypeService(this.beanVariety).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        setTimeout(() => {
          window.history.go(-1);
        }, 1000)
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        this.isDisable = false;
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
      this.isDisable = false;
    })
  }
  //取消
  cancel() {
    window.history.go(-1);
  }
  doChoose() {
    if (this.beanVariety.intRateSrcCode == '1') {
      this.beanVariety.intRateFlgCode = '01';
      this.show=true;
    }else{
      this.beanVariety.intRateFlgCode = '';
      this.show=false;
    }
  }

  codeValue(val, arr: any = []) {
    let temp = "";
    if (arr.length > 0) {
      arr.forEach(item => {
        if (item['value'] == val) {
          temp = item['label']
        }
      });
    }
    return temp
  }

}
