  param: any;
   //获取所有码值
   code: any;
  constructor(public route: ActivatedRoute,
    public httpService: ValuationHttpService,
    public ce: ChangeDetectorRef,
    public commfunc: Commfunc
  ) {
    this.codeValues();
    this.isOrigin=this.code['IsOrigin'];
    this.isLable2=this.code['IsLable2'];
  }
  ngOnInit() {
    let params = {};
    if (this.inValue) {
      params = JSON.parse(this.inValue);
    }
    let param = {
      intRateCgy: params['intRateCgy'],
      id: params['id']
    };
    //查询方法
    this.httpService.queryRateTypeByIdService(param).subscribe(data => {
      if (data.resultMap) {
        this.beanVariety = data.resultMap;
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
    //码值
    codeValues() {
      this.commfunc.codeValue();
      let ret = this.commfunc.codeDatas;
      return this.code = ret //code是自己声明的变量
    }
