import { Component, OnInit } from '@angular/core';
import { BeanVariety } from './bean/base-variety.bean';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
// import { IsOrigin, IsLable2, IsRcord } from './../constant/codeValue';
import { Router } from '@angular/router';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";

@Component({
  selector: 'app-base-variety',
  templateUrl: './base-variety.component.html',
  styleUrls: ['./base-variety.component.scss'],
  providers: [ConfirmationService, Commfunc]

})
export class BaseVariety implements OnInit {
  beanVariety: BeanVariety = new BeanVariety();
  //提示信息
  msgs: Message[] = [];
  //table数据
  list: any = [];
  //传入详情的值
  inValueCode: string;
  totalNum: string;
  //模态框
  display: boolean = false;
  //1 更新 2添加 3详情
  showModel: string;
  //弹出框的头信息
  headerTitle: string;
  isOrigin: any = [];
  isLable2: any = [];
  isRcord: any = []; I
  total: number = 10;
  first: number = 0;//分页控制
  tabMesg: any = tableMessage;//表格中无数据时显示的内容
  //获取所有码值
  code: any;
  constructor(
    public httpService: ValuationHttpService,
    public confirmationService: ConfirmationService,
    public router: Router,
    public commfunc: Commfunc
  ) {
    this.codeValues();
    this.isOrigin=this.code['IsOrigin'];
    this.isLable2=this.code['IsLable2'];
    this.isRcord=this.code['IsRcord'];
  }
  ngOnInit() {
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para');
    }
    this.beanVariety.pageSize = 10;
    this.beanVariety.pageNum = 1;
  }
  querySecle() {
    this.beanVariety.pageSize = 10;
    this.beanVariety.pageNum = 1;
    this.first = 0;
    this.query();
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  query() {
    this.httpService.queryRateTypeInternalService(this.beanVariety).subscribe(data => {
      if (data.resultList == "" || data.resultList == null) {
        this.tabMesg = tableMessage
      }
      this.list = data.resultList
      this.total = data.total;
    }, err => {
      this.tabMesg = tableMessage
      // this.list = [{ intRateCgy: '01' }, { intRateCgy: '01' }];
      // this.msgs = [];
      // this.msgs.push({ severity: 'error', summary: '提示', detail: "调服务失败" });
    })
  }
  //重置
  newSet() {
    this.beanVariety = new BeanVariety();
    this.beanVariety.pageSize = 10;
    this.beanVariety.pageNum = 1;
    // this.first = 0;
    // this.query();
  }
  add(param) {
    this.headerTitle = '基础利率种类新增';
    this.router.navigate(['/pages/tzb/valuation/base-variety/base-variety-add'])
  }
  //详情
  detail(param) {
    this.headerTitle = '基础利率种类-详情';
    this.showModel = '2';
    this.display = true;
    if (param) {
      this.inValueCode = JSON.stringify(param);
    }
  }
  //详情的回调方法
  detailCall(param) {
    this.display = param;
  }
  //修改
  update(param) {
    this.headerTitle = '基础利率种类-修改';
    this.showModel = '1';
    this.display = true;
    if (param) {
      this.inValueCode = JSON.stringify(param);
    }
  }
  //修改的回掉函数
  updateCall(param) {
    this.display = false;
    if (param != false) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param });
    }
    this.query();
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.beanVariety.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.beanVariety.pageNum = currentPage;
    this.first = (currentPage - 1) * this.beanVariety.pageSize;
    //调用查询的方法
    this.query();
    this.beanVariety.pageNum = 1;
  }
  //删除
  delete(car) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = {
          intRateCgy: car.intRateCgy,
          id: car.id,
        };
        this.httpService.deleteRateTypeService(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.first = 0;
            this.query();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
      }
    });
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}
