import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { BaseVariety } from "./base-variety.component";
import { BaseVarietyAddComponent } from "./base-variety-add/base-variety-add.component";
// import { BaseVarietyDelete } from "./base-variety-delete/base-variety-delete.component";

export const routes: Routes = [
    {
        path: '',component: BaseVariety,
    },  
    {path:'base-variety-add',component:BaseVarietyAddComponent},
    // {path:'base-variety-delete',component:BaseVarietyDelete}
];



export const BaseVarietyRouting: ModuleWithProviders = RouterModule.forChild(routes);
